
import Vue from 'vue'
import App from './App'
import router from './router'
import $api from './api.js'

Vue.config.productionTip = false

Vue.prototype.$api = $api

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render:h =>h(App),
  router
})



