<template>
  <div class="productDetail">

    <HeaderTop title="商品详情">
      <a class="aui-btn aui-pull-left" slot="left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
    </HeaderTop>

    <nav class="msite_nav border-1px">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="item in product.goodsImgList">
            <img :src="'http://'+item.goodsImgImg"/>
          </div>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </nav>

    <div class="aui-list-item z-margin-t5">
      <div class="aui-media-list-item-inner">

        <div class="aui-list-item-inner">
          <div class="aui-list-item-text z-detail-tit aui-ellipsis-2">
            {{product.goodsTitle}}
          </div>
          <div class="aui-info aui-font-size-12 z-comment-detail bg-background">
            <div class="aui-info-item" style="padding-left: 20px;">
              <span class="fontcolor aui-font-size-18 z-colorshenhong">{{product.goodsCurrnetScore}}积分</span>
              <span class="z-margin-l1 z-del">{{product.goodsOriginScore}}积分</span>
            </div>


          </div>


          <div class="aui-list aui-list-in white z-margin-t5">

            <div class="aui-list-item">
              <div class="aui-list-item-inner aui-list-item-arrow">
                <div class="aui-list-item-title aui-font-size-12">服务</div>
                <div class="aui-list-item-right">保证正品，一年保修</div>
              </div>
            </div>


          </div>
          <div class="aui-info aui-font-size-12 z-comment-detail bg-background">
            <div class="aui-info-item z-color_hei" style="padding-left: 18px;">
              <span>商家：</span>
              <span>{{product.goodsBusiness}}</span>
            </div>

          </div>

          <div class="aui-info aui-font-size-12 z-comment-detail z-margin-t5 bg-background">
            <div class="aui-info-item z-color_hei" style="padding-left: 18px;">商品详情</div>
          </div>
          <div class="aui-card-list-content z-padding-b5">
            <div class="aui-row aui-row-padded">
              <div class="aui-col-xs-12">
                <img :src="'http://'+product.goodsDescribe">
                
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--底部-->
    <div class="" style="width: 100%;position: fixed;bottom: 0;margin: auto;bottom: 40px;">

      <router-link :to="'/orderfirm/'+$route.params.id">
        <div class="aui-btn  aui-btn-block aui-btn-sm aui-margin-t-15 colorWhite" style="width: 90%;
    border-radius: 30px;
    margin: auto;background: #ff5d47;">立即兑换</div>

      </router-link>


    </div>


  </div>

</template>

<script>
  import Swiper from 'swiper'
  import 'swiper/dist/css/swiper.min.css'
  import HeaderTop from '../../../components/HeaderTop/HeaderTop'
  export default {
    name: "productDetail",
		data(){
			return{
				product:{}
			}
		},
		created(){
			this.$api.getProductDetail({
				'goodsImgGoodsId':this.$route.params.id
			}).then(res=>{
				this.product = res.data.goods
			})
		},
    mounted() {
			console.log(this.product)
      // 轮播图的实现事件
			
      new Swiper('.swiper-container', {
        pagination: {
          el: '.swiper-pagination',
        },
        loop: true,
				observer:true,//修改swiper自己或子元素时，自动初始化swiper
				observeParents:true//修改swiper的父元素时，自动初始化swiper
      })
    },
    components:{
      HeaderTop
    }
		
  }
</script>

<style scoped>
  /*轮播图样式*/
	.productDetail{
		padding-top: 2.25rem;
	}
  .msite_nav {
    /*margin-top: 45px;*/
    height: 200px;
    background: #fff;
  }

  .msite_nav .swiper-container {
    width: 100%;
    height: 100%;
  }

  .msite_nav .swiper-container .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
  }
	.msite_nav .swiper-container .swiper-slide img{
		width: 100%;
	}

  .msite_nav .swiper-container .swiper-slide .link_to_food {
    width: 25%;
  }

  .food_container {
    display: block;
    width: 100%;
    text-align: center;
    padding-bottom: 10px;
    font-size: 0;
  }

  .food_container img {
    display: inline-block;
    width: 50px;
    height: 50px;
  }

  .swiper-slide .link_to_food span {
    display: block;
    width: 100%;
    text-align: center;
    font-size: 13px;
    color: #666
  }

  .swiper-pagination > span.swiper-pagination-bullet-active {
    background: #02a774
  }


</style>



