<template>
    <div class="Saoma z-bg-hui">
      <!--头部-->
      <header class="aui-bar aui-bar-nav z-bar-sao-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">扫码</div>
        <a class="aui-btn aui-pull-right">
          <span class="aui-iconfont aui-icon-camera"></span>
        </a>
      </header>
      <!--扫码区域-->
      <div class="z-sao-box z-margin-t3">
        <p class="z-sao-p"></p>
      </div>

      <div class="z-sao-box z-margin-t70">
        <img class="z-sao-img" src="../../../static/images/icon_saomaanniu.png">
      </div>

        <div class="z-sao-box z-input-p">
          <input class="z-sao-inpt" style="" type="text" placeholder="请输入机器码">
        </div>
    </div>
</template>

<script>
    export default {
        name: "Saoma"
    }
</script>

<style scoped>
  /*头部导航条*/
  .aui-bar-nav {
    background: #000;
  }
  .z-bg-hui{background: #4b4b4b!important;}
  .z-input-p{margin: 10px auto;padding-bottom: 200px;}

</style>



